<template>
  <div class="form-mode">
    <gs-form
      ref="form"
      :model="formData"
      :rules="rules"
      label-width="90px"
    >
      <gs-form-item
        label="数据库"
        prop="database"
        required
      >
        <gs-select
          v-model="formData.database"
          placeholder="请选择数据库"
        >
          <gs-option
            v-for="(database, index) in databases"
            :key="index"
            :label="database.label"
            :value="database.label"
          />
        </gs-select>
      </gs-form-item>

      <gs-form-item
        label="表名称"
        prop="tableName"
        required
      >
        <gs-input
          v-model="formData.tableName"
          placeholder="请输入表名称"
        />
      </gs-form-item>

      <gs-form-item
        label="类型"
        prop="tableType"
      >
        <gs-radio-group
          v-model="formData.tableType"
          @change="handleTableTypeChange"
        >
          <gs-radio :label="1">外部表</gs-radio>
          <gs-radio :label="2">内部表</gs-radio>
        </gs-radio-group>
      </gs-form-item>

      <!-- <gs-form-item
        v-if="formData.tableType === 2"
        label="表生命周期"
        prop="lifecycle"
      >
        <gs-radio-group v-model="formData.lifecycle">
          <gs-radio :label="1">永久</gs-radio>
          <gs-radio :label="2">自定义</gs-radio>
          <gs-input-number
            v-if="formData.lifecycle === 2"
            v-model="formData.expireDate"
            min="0"
            placeholder="天"
          />
        </gs-radio-group>
      </gs-form-item> -->

      <gs-form-item
        v-if="formData.tableType === 1"
        key="hdfsPath"
        label="HDFS路径"
        prop="hdfsPath"
      >
        <gs-input
          v-model="formData.hdfsPath"
          placeholder="请输入HDFS路径"
        />
      </gs-form-item>

      <gs-form-item
        key="fieldsTerminated"
        label="字段分隔符"
        prop="fieldsTerminated"
      >
        <gs-input
          v-model="formData.fieldsTerminated"
          placeholder="请输入字段分隔符"
        />
      </gs-form-item>

      <gs-form-item
        key="lineTerminated"
        label="行分隔符"
        prop="lineTerminated"
      >
        <gs-input
          v-model="formData.lineTerminated"
          placeholder="请输入行分隔符"
        />
      </gs-form-item>

      <!-- <gs-form-item
        label="模式"
        prop="fieldMode"
      >
        <gs-radio-group v-model="formData.fieldMode">
          <gs-radio :label="1">手动添加字段</gs-radio>
        </gs-radio-group>
      </gs-form-item> -->

      <gs-form-item
        class="field-table-item"
        label="普通字段"
        prop="fields"
        required
      >
        <field-table v-model="formData.fields" />
      </gs-form-item>

      <gs-form-item
        label="分区表"
        prop="isPartition"
      >
        <gs-checkbox-group v-model="formData.isPartition">
          <gs-checkbox />
        </gs-checkbox-group>
      </gs-form-item>

      <gs-form-item
        v-if="formData.isPartition"
        class="field-table-item"
        label="分区字段"
        prop="partitionFields"
        required
      >
        <field-table v-model="formData.partitionFields" />
      </gs-form-item>

      <gs-form-item
        label="HDFS文件格式"
        prop="storedFormat"
      >
        <gs-select
          v-model="formData.storedFormat"
          placeholder="请选择HDFS文件格式"
        >
          <gs-option
            v-for="(item, index) in fileTypes"
            :key="index"
            :label="item"
            :value="item"
          />
        </gs-select>
      </gs-form-item>
    </gs-form>
  </div>
</template>

<script>
import FieldTable from './field-table';

const re = /^[a-zA-Z$_][a-zA-Z\d_]*$/;

export default {
  components: {
    FieldTable
  },

  props: {
    value: {
      type: Object,
      default() {
        return {};
      }
    },
    databases: {
      type: Array,
      default() {
        return {};
      }
    }
  },

  data() {
    const filedsValidator = (rule, value, cb) => {
      if (!value || value.length === 0) {
        cb(new Error('字段不能为空'));
      } else {
        for (let i = 0; i < value.length; i++) {
          const columnName = value[i].columnName;

          if (!columnName) {
            cb(new Error(`第${i + 1}行的列名不能为空`));
            return;
          } else if (!re.test(columnName)) {
            cb(new Error(`第${i + 1}行的列名不合法`));
            return;
          }
        }
        cb();
      }
    };
    const terminatedValidator = (rule, value, cb) => {
      if (!value) {
        cb(new Error('字段分隔符不能为空'));
      } else if (/\\n/.test(value)) {
        cb(new Error('字段分隔符不能为换行符'));
      } else {
        cb();
      }
    };

    return {
      formData: this.value,
      fileTypes: ['avro', 'parquet', 'textfile', 'sequencefile', 'orc'],
      requiredTerminated: true,
      rules: {
        tableName: [
          {
            validator: (rule, value, cb) => {
              if (!value) {
                cb(new Error('表名不能为空'));
              } else if (!re.test(value)) {
                cb(new Error('请输入合法的表名'));
              } else {
                cb();
              }
            }
          }
        ],
        hdfsPath: [
          {
            message: 'HDFS路径不能为空',
            required: true
          }
        ],
        fields: [
          {
            validator: filedsValidator
          }
        ],
        partitionFields: [
          {
            validator: filedsValidator
          }
        ],
        fieldsTerminated: [
          {
            required: true,
            validator: terminatedValidator
          }
        ]
      }
    };
  },

  watch: {
    value(val) {
      this.formData = val;
    }
  },

  mounted() {
    this.$watch('formData', () => {
      this.$emit('input', this.formData);
    }, {
      deep: true
    });
  },

  methods: {
    handleTableTypeChange(val) {
      this.rules.fieldsTerminated[0].required = val === 1;
      if (val === 2) {
        this.$refs.form.validateField('fieldsTerminated');
      }
    }
  }
};
</script>




