<template>
  <div class="sql-mode">
    <gs-form
      ref="form"
      :model="sqlData"
      label-width="80px"
    >
      <gs-form-item
        label="数据库"
        prop="database"
      >
        <gs-select
          v-model="sqlData.database"
          placeholder="请选择数据库"
          disabled
        >
          <gs-option
            v-for="(database, index) in databases"
            :key="index"
            :label="database.label"
            :value="database.label"
          />
        </gs-select>
      </gs-form-item>
    </gs-form>
    <div class="codemirror-input">
      <label class="codemirror-input-label">建表语句</label>
      <codemirror
        v-model="sqlData.sql"
        :options="cmOptions"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Object,
      default() {
        return {};
      }
    },
    databases: {
      type: Array,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      sqlData: this.value,
      cmOptions: {
        readOnly: 'nocursor',
        tabSize: 4,
        mode: 'text/x-hive',
        styleActiveLine: true,
        lineNumbers: true,
        line: true
      }
    };
  },

  watch: {
    value(val) {
      this.sqlData = val;
    }
  }
};
</script>




