<template>
  <div class="field-table">
    <el-table
      :data="dataSources"
      border
    >
      <el-table-column
        prop="columnName"
        label="列名"
        width="180"
      >
        <template slot-scope="scope">
          <gs-input
            ref="columnInput"
            v-model="scope.row.columnName"
            :validate-event="!isValid(scope.row.columnName)"
            placeholder="请输入列名"
            @change="handleColumnInput(scope)"
          />
        </template>
      </el-table-column>
      <el-table-column
        prop="description"
        label="描述"
        width="180"
      >
        <template slot-scope="scope">
          <gs-input
            v-model="scope.row.description"
            :validate-event="false"
            placeholder="请输入字段描述"
            @change="handleColumnInput(scope)"
          />
        </template>
      </el-table-column>
      <el-table-column
        prop="type"
        label="类型"
      >
        <template slot-scope="scope">
          <gs-select
            v-model="scope.row.type"
            :validate-event="false"
            placeholder="请选择数据库"
            @change="handleColumnInput(scope)"
          >
            <gs-option
              v-for="(item, index) in fieldTypes"
              :key="index"
              :label="item"
              :value="item"
            />
          </gs-select>
        </template>
      </el-table-column>
      <el-table-column
        prop="id"
        label="操作"
      >
        <template slot-scope="scope">
          <action-comfirm
            title="确定删除该字段吗？"
            @comfirm="deleteField(scope)"
          >
            <span
              slot="reference"
              class="remove-icon"
            >
              <gs-icon name="close" />删除
            </span>
          </action-comfirm>
        </template>
      </el-table-column>
    </el-table>

    <span
      class="add-icon"
      @click="addField"
    >
      <gs-icon name="plus-circle" /><span>添加</span>
    </span>
  </div>
</template>

<script>
import emitter from '@gs-ui/gs-ui/lib/_utils/mixins/emitter';
import ActionComfirm from 'src/components/action-comfirm';

export default {
  components: {
    ActionComfirm
  },

  mixins: [emitter],

  props: {
    value: {
      type: Array,
      default() {
        return [];
      }
    }
  },

  data() {
    return {
      fieldTypes: [
        'BOOLEAN',
        'TINYINT',
        'SMALLINT',
        'INT',
        'BIGINT',
        'FLOAT',
        'DOUBLE',
        'DECIMAL',
        'STRING',
        'BINARY',
        'TIMESTAMP',
        'DATE'
      ],
      dataSources: this.value
    };
  },

  watch: {
    value(val) {
      this.dataSources = val;
    }
  },

  methods: {
    isValid(value) {
      const re = /^[a-zA-Z$_][a-zA-Z\d_]*$/;

      return value && re.test(value);
    },

    addField() {
      this.dataSources.push({
        columnName: '',
        description: '',
        type: this.fieldTypes[0]
      });
      this.$emit('input', this.dataSources);
      this.$nextTick(() => {
        this.$refs.columnInput.focus();
      });
    },

    deleteField(scope) {
      this.dataSources.splice(scope.$index, 1);
      this.$emit('input', this.dataSources);
      this.dispatch('GsFormItem', 'gs.form.change', this.dataSources);
    },

    handleColumnInput(scope) {
      const {
        $index,
        row
      } = scope;

      this.dataSources[$index] = row;
      this.$emit('input', this.dataSources);
    }
  }
};
</script>




