<template>
  <div class="table-create-page">
    <h3 class="table-create-title">新建离线表</h3>
    <gs-tabs
      v-model="activeName"
      type="content-tab"
    >
      <gs-tab-pane
        label="表单模式"
        name="form"
      >
        <form-mode
          ref="formMode"
          :databases="databases.list"
          v-model="formData"
        />
      </gs-tab-pane>
      <gs-tab-pane
        label="SQL模式"
        name="sql"
      >
        <sql-mode
          ref="sqlMode"
          :databases="databases.list"
          :value="sqlData"
        />
      </gs-tab-pane>
    </gs-tabs>

    <div class="sql-scan">
      <gs-button
        v-if="activeName !== 'sql'"
        type="text-primary"
        @click="handleScan"
      >预览</gs-button>
    </div>

    <div class="action-btns">
      <action-comfirm
        title="确定新建离线表吗？"
        @comfirm="createTable"
      >
        <gs-button
          slot="reference"
          :loading="isCreating"
          type="primary"
        >确定</gs-button>
      </action-comfirm>
      <action-comfirm
        title="确定取消新建离线表吗？"
        @comfirm="cancelCreate"
      >
        <gs-button slot="reference">取消</gs-button>
      </action-comfirm>
    </div>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import ActionComfirm from 'src/components/action-comfirm';
import FormMode from './form-mode';
import SqlMode from './sql-mode';
import './style';

export default {
  components: {
    ActionComfirm,
    FormMode,
    SqlMode
  },

  data() {
    return {
      isCreating: false,
      activeName: 'form',
      formData: {
        database: '',
        tableName: '',
        tableType: 2,
        lifecycle: 1,
        expireDate: -1,
        hdfsPath: '',
        // fieldMode: 1,
        fields: [],
        isPartition: false,
        partitionFields: [],
        storedFormat: '',
        fieldsTerminated: '',
        lineTerminated: '\\n'
      },
      sqlData: {
        database: '',
        sql: ''
      }
    };
  },

  computed: {
    ...mapState('metadata', {
      databases: 'databases'
    })
  },

  watch: {
    activeName(val) {
      const {
        formData
      } = this;

      if (val === 'sql') {
        this.transformForm2Sql(formData).then((sql) => {
          this.sqlData = {
            database: formData.database,
            sql
          };
        });
      }
    },
    databases(val) {
      const list = val.list;

      if (list && list.length > 0) {
        const database = list[0].label;

        this.formData.database = database;
        this.sqlData.database = database;
      }
    }
  },

  mounted() {
    this.getDatabases();
  },

  methods: {
    ...mapActions('table', [
      'createTableByForm',
      'createTableBySql',
      'transformForm2Sql'
    ]),
    ...mapActions('metadata', [
      'getDatabases'
    ]),

    form2sql() {
      const {
        tableName,
        type,
        // lifecycle,
        // expireDate,
        hdfsPath,
        // fieldMode,
        fields,
        isPartition,
        partitionFields,
        storedFormat
      } = this.formData;
      const external = type === 1;
      const TABLE = `CREATE ${external ? 'EXTERNAL ' : ''}TABLE ${tableName} \n`;
      const FIELDS = this.createFieldStatement(fields);
      const STORED = storedFormat ? `STORED AS ${storedFormat} \n` : '';
      const LOCATION = hdfsPath ? `LOCATION ${hdfsPath};` : '';
      const PARTITIONED = isPartition && partitionFields.length > 0
        ? `PARTITIONED BY \n${this.createFieldStatement(partitionFields)}` : '';

      return [
        TABLE,
        FIELDS,
        PARTITIONED,
        STORED,
        LOCATION
      ].join('');
    },

    createFieldStatement(fields) {
      const statement = fields.map(field => {
        const {
          columnName,
          desc,
          type
        } = field;
        let sql = `    ${columnName} ${type}`;

        if (desc) {
          sql += ` COMMENT '${desc}'`;
        }
        return sql;
      }).join('\n');

      if (!fields || fields.length === 0) {
        return '';
      }
      return `(\n${statement}\n)\n`;
    },

    handleScan() {
      this.activeName = 'sql';
    },

    createTable() {
      const success = (res) => {
        this.$Message.success('新建成功，正在跳转到表信息页...', 1.5);
        setTimeout(() => {
          this.$router.push({
            path: `/main/metadata/detail/${res.tableId}`
          });
        }, 1500);
      };
      const fail = () => {
        this.isCreating = false;
      };

      if (this.activeName === 'form') {
        const form = this.$refs.formMode.$refs.form;

        form.validate((isValid) => {
          if (isValid) {
            this.isCreating = true;
            this.createTableByForm(this.formData).then(success, fail);
          }
        });
      } else {
        this.isCreating = true;
        this.createTableBySql({ sql: this.sqlData.sql }).then(success, fail);
      }
    },

    cancelCreate() {
      this.$router.push({
        path: '/main/metadata'
      });
    }
  }
};
</script>




